import React from 'react';
import { MyContext } from '../Context/Context';
import Header from './Header';

const Contact = () => {
    const { state, setUserName } = React.useContext(MyContext);
    
    React.useEffect(() => {
        const temp = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : null;
        setUserName(temp);
    }, []);
    
  return (
    <div>
        <Header />
         <h1>Contact</h1>
    </div>
  )
}


export default Contact;