import React, { useEffect, useState } from 'react';
import { Button, Card, ListGroup } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Header from './Header';
import { MyContext } from '../Context/Context';
import axios from 'axios';
import NoData from './NoData';
import Loader from '../Components/Loader';

const styles = {
    width: '100%',
    height: '100vh',
    backgroundColor: 'purple',
    color: 'white',
    position: 'absolute'
};

const ContentStyles = {
    backgroundColor: 'grey',
    marginLeft: '50%',
    transform: 'translate(-50%)',
    marginTop: '1%',
    textAlign: 'center'
};

const HomePage = () => {
    const [loader, setLoader] = useState(false);
    const { state, setUserName } = React.useContext(MyContext);
    const [products, setProducts] = useState([]);

    useEffect(() => {
        const temp = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : null;
        setUserName(temp);
    }, []);

    useEffect(() => {
        setLoader(true);
        try {
            axios.get("https://dummyjson.com/products")
            .then((response) => {
                if(response.status === 200) {
                    setLoader(false);
                    setProducts(response.data.products);
                    console.log(response.data.products)
                } else {
                    setLoader(false);
                    setProducts([]);
                }
            })
            .catch((error) => {
                setLoader(false);
                setProducts([]);
                console.log(error)
            });
        } catch(error) {
            setLoader(false);
            setProducts([]);
            console.log(error);
        }
    }, []);

    return (
        <>
            {loader && <Loader />}
            <Header />
            <div className="main">
                {products.length > 0 ?
                    (
                        <>
                            {products.map((data) => {
                                return (
                                    <Card style={{ width: '18rem' }}>
                                        <Card.Img variant="top" src={data.thumbnail} />
                                        <Card.Body>
                                            <Card.Title>{data.title}</Card.Title>
                                            <Card.Text>
                                                {data.description}
                                            </Card.Text>
                                        </Card.Body>
                                        <ListGroup className="list-group-flush">
                                            <ListGroup.Item>Price: {data.price}</ListGroup.Item>
                                            <ListGroup.Item>Rating: {data.rating}</ListGroup.Item>
                                            <ListGroup.Item>Stock: {data.stock}</ListGroup.Item>
                                            <ListGroup.Item>Category: {data.category}</ListGroup.Item>
                                            <ListGroup.Item>Brand: {data.brand}</ListGroup.Item>
                                            <ListGroup.Item>Discount: {data.discountPercentage}</ListGroup.Item>
                                        </ListGroup>
                                        <Card.Body>
                                            <Card.Link href="#">...more</Card.Link>
                                        </Card.Body>
                                    </Card>
                                )
                            })}
                        </>
                    ) : (
                        <NoData />
                    )
                }
            </div>
        </>
    )
}

export default HomePage;