import React from 'react';
import { Form } from 'react-bootstrap';

const InputGroup = ({
    type,
    name,
    id,
    className,
    text,
    onChange,
    placeholder,
    controlId
}) => {
  return (
    <Form.Group controlId={controlId}>
      <Form.Control
          type={type}
          placeholder={placeholder}
          name={name}
          onChange={onChange}
      />
    </Form.Group>
  )
}

export default InputGroup;