import React, { useEffect, useState } from 'react';
import Image1 from '../../images/login.jpg';
import { Col, Container, Row } from 'react-bootstrap';
import InputGroup from '../../Components/InputGroup';
import CustomButton from '../../Components/Button';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import ErrorMessage from '../../Components/ErrorMessage';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { MyContext } from '../../Context/Context';

const Login = () => {
  const { setUserName } = React.useContext(MyContext);
  let timer = 0;
  const navigate = useNavigate();

  const [loginError, setLoginError] = useState('');
  const token = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : null;
  
  const formik = useFormik({
    initialValues: {
      email: '',
      password: ''
    },
    validationSchema: Yup.object().shape({
      email: Yup.string().email("Invalid Email").required("Email ID requied"),
      password: Yup.string().required("Password required")
    }),
    onSubmit: values => {
      validation(values);
    }
  });

  const validation = (values) => {
    try {
      axios.get("https://myjson.dit.upm.es/api/bins/g35h")
        .then((response) => {
          if(response.status === 200) {
            const data = response.data;
            let exist = {};
            data.forEach((val) => {
              if(val.username === values.username && val.password === values.password) {
                exist = val;
              }
            });
            if(Object.keys(exist).length > 0) {
              localStorage.setItem("user", JSON.stringify(exist));
              setUserName(exist); // set context api
              navigate('/home');
            } else {
              setLoginError("User not found.");
              timer = setTimeout(() => {
                clearErrorMsg();
              },3000);
            }
          } else {
            setLoginError("Something went wrong. Please try again later.");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } catch(error) {
      console.log(error);
    }
  }

  const clearErrorMsg = () => {
    clearTimeout(timer);
    setLoginError("");
  }

  useEffect(() => {
    if(token && Object.keys(token).length > 0) {
      navigate("/home");
    }
  }, []);

  return (
    <Container className="mt-5 pt-5">
      <Row>
        <Col lg={4} md={4} sm={12} className="text-center mt-5 pt-5">
          <form onSubmit={formik.handleSubmit} className="d-grid gap-4">
            <InputGroup
              formGroupId="formBasicEmail"
              type="text"
              placeholder="Enter email id"
              name="email"
              value={formik.values.email}
              onChange={formik.handleChange}
            />
            <ErrorMessage
              touched={formik.touched.email}
              errors={formik.errors.email}
            />
            <InputGroup
              formGroupId="formBasicPassword"
              type="password"
              placeholder="Enter password"
              name="password"
              value={formik.values.password}
              onChange={formik.handleChange}
            />
            <ErrorMessage
              touched={formik.touched.password}
              errors={formik.errors.password}
            />
            <CustomButton
              variant="primary"
              size="lg"
              type="submit"
              text="Login"
            />
            {loginError && <div style={{color: 'red'}}>{loginError}</div>}
          </form>
        </Col>
        <Col lg={8} md={6} sm={12}>
          <img src={Image1} className="w-100" alt="Login" />
        </Col>
      </Row>
    </Container>
  )
}

export default Login;