import React, { useContext } from 'react';
import Login from './Views/Login/Login';
import {HashRouter, Routes, Route, Link} from 'react-router-dom';
import DNE from './Views/DNE';
import ProtectedRoutes from './Components/ProtectedRoutes';
import HomePage from './Views/HomePage';
import About from './Views/About';
import Contact from './Views/Contact';
import Blog from './Views/Blog';
import { MyContext } from './Context/Context';

function App() {
  return (
    <div>
      <HashRouter>
        <Routes>
          <Route exact strict path="/" element={<Login />} />
          <Route exact strict path="/login" element={<Login />} />
          <Route exact strict path="/home" element={
            <ProtectedRoutes>
              <HomePage />
            </ProtectedRoutes>
          } />
          <Route exact strict path="/about" element={
            <ProtectedRoutes>
              <About />
            </ProtectedRoutes>
          } />
          <Route exact strict path="/contact" element={
            <ProtectedRoutes>
              <Contact />
            </ProtectedRoutes>
          } />
          <Route exact strict path="/blog" element={
            <ProtectedRoutes>
              <Blog />
            </ProtectedRoutes>
          } />
          <Route exact strict path="*" element={<DNE />} />
        </Routes>
      </HashRouter>
    </div>
  );
}

export default App;